					<img src="../assets/images/about1.jpeg" class="img-full-width observe slide-left top-page-image">
				</v-flex>
				<v-flex xs12 md6 class="slide-right observe pa-4">
					<p class="text-secondary mb-3 content-text">Poklic vzgojiteljice sem imela zelo rada, prav tako sem se rada igrala. e kot otrok sem sama ivala oblekice za punke, izdelovala posteljice, vozike iz katel za evlje. Prvo punko mi je prinesel Miklav. Enkrat sem dobila klik, da bi rada tisto punko. Seveda jo ni bilo ve. Ta trenutek me je prevzela elja, da bi zbirala punke. la sem na boljake in v starinarnice ter kupila prve punke. Mo je dal narediti slaiarki punko za na torto. Naredila jo je iz marcipana in je stara 17 let. Punke zbiram 20 let. Imam jih e 526. Sedaj mi jih podarjajo obiskovalci ali prinesejo prijatelji iz izletov iz tujine. Punke so okrasne in igralne.</p>
					<p class="text-secondary mb-3 content-text">Igralne punke so stare od 40 do 110 let. Prve so bile narejene iz papirmascheja, kasneje iz celulojda, gume, e kasneje iz plastike. Kaken oe je izrezljal celo leseno igralno punko, mame pa so seile punke iz blaga. Punke so obleene tako kot sem jih dobila. Redke imajo original oblaila, kajti otroci so se igrali in so se uniile. Veina punk ima doma seite oblekice, spletene iz volne in kvakane.</p>
					<p class="text-secondary mb-3 content-text">Okrasne punke so predvsem porcelanaste in italjanske bambule. Bambule so imeli za okras na posteljah. Skoraj v vseh gospodinjstvih so jih imeli.</p>
					<p class="text-secondary mb-3 content-text">Porcelanaste punke so zelo lepega videza, imajo prekrasna oblaila, so velike in majhne. V zbirki je kar nekaj porcelanastih punk, ki so zelo izrazne( alostne, vesele, jezne, vane, zavistne, zaljubljene).</p>
					<p class="text-secondary mb-3 content-text">V zbirki so punke iz vseh Evropskih drav.</p>
					<p class="text-secondary mb-3 content-text">Potem so pa e punke obleene v slovenske narodne noe in kot spominske iz razlinih evropskih drav. </p>
				</v-flex>
			</v-layout>
		</div>
