					<p class="text-secondary mb-3 content-text">Kasneje sem zaela zbirati vse igrae. Tako, da se je nabrala tudi zanimiva zbirka starih medvedkov, konjikov, kuhinj, vozikov,  didaktinih igra itd. </p>
					<p class="text-secondary mb-3 content-text">Ljudje pa neradi zavrejo spomine na otrotvo zato so mi zaeli prinaati e vse, kar so uporabljali za otroka: otroke vozike, koare,posteljice, zibke, stoj'ce, oblaila za krst itd. </p>
					<p class="text-secondary mb-3 content-text">"Kdor zavre igrao, zavre otrotvo". Zato je res bolje, da oddaste zelo stare predmete v muzeje, da se ohranijo kot kulturna dediina, kot da jih zavrete. Predmet nima vrednosti, e je nekje na podstrehi, vrednost dobi ko je razstavljen.</p>
